import React, { Component } from 'react';
import Scroll from '../../components/Scroll';

export default class Overview extends Component<any, any> {
	render() {
		return (
			<div className="page" id="home-overview">
				<Scroll className="text">
					<h2>Willkommen im DSA5 Heldentool!</h2>

					<h3>Viele Anpassungsmglichkeiten</h3>
					<p>
						Du kannst dir die Seiten immer so anzeigen lassen, wie du sie gerade bentigst. Das geht mit Textfiltern, Sortierfiltern und Kategoriefiltern. Aber auch empfohlene Fhigkeiten, wie sie z.B. bei einer Kultur vorkommen, knnen angezeigt werden. Falls du meinst, dass eine Option fehlt, die Arbeit ersparen wrde, zgere nicht, uns deinen Vorschlag mitzuteilen!
					</p>
					
					<h3>Testen</h3>
					<p>
						Du mchtest dir nicht sofort ein Konto erstellen, sondern erstmal die Funktionen testen? Kein Problem! Whle oben rechts einfach "Testen" aus. Du kannst dir damit testweise einen Helden erstellen, allerdings nicht speichern, drucken oder ein Avatarbild hochladen.
					</p>
					<p>
						Wenn du dich dazu entscheidest, ein Konto zu erstellen, kannst du deinen soeben erstellten Helden auch speichern und damit dann alle weiteren Funktionen nutzen. Du darfst vorher nur nicht diese Seite verlassen oder neu geladen haben!
					</p>

					<h3>Helden teilen</h3>
					<p>
						Du wollt, dass auch mal jemand anderes einen Blick auf deinen Helden werfen kann, ohne dir ber die Schulter zu schauen? Du wirst seinem Account in einem spteren Patch die Erlaubnis erteilen knnen, dies zu tun. Aber keine Angst, er wird nichts an ihm verndern knnen. OP bleibt OP.
					</p>

					<h3>Gruppen</h3>
					<p>
						Wir werden in einem spteren Patch eine Gruppenverwaltung hinzufgen. Dadurch knnen Gruppenmitglieder bersichtlich angezeigt und verglichen werden. Auerdem kann dieses Tool auch in-game dazu genutzt werden, die INI-Tabelle in Kombination mit Zustands- und Energieanzeigen darzustellen. Diese Ingame-Tabelle hatten wir vorher einmal unter dem Namen "GameApp" verffentlicht. Wir werden dieses Tool fr DSA5 umschreiben.
					</p>
					
					<h3>Ein Wort (vielleicht auch mehr) zur Browserkompatibilitt</h3>
					<p>
						Der Browser wurde mit den neuesten Versionen von Edge, Firefox und Chrome getestet. Du solltest keinen lteren Browser verwenden, da dort mglicherweise (mehr) Fehler (als sonst) auftreten knnen! Die App ist (noch) nicht auf Mobilgerte angepasst worden. Die Funktionalitt kann im Vergleich zum Desktopbrowser auch unterschiedlich sein!
					</p>
				</Scroll>
			</div>
		);
	}
}



